import Uncheck from '@/static/img/icon-uncheck.png'
import Checked from '@/static/img/icon-checked.png'
import T from '@/utils/tips.js'
import {
	payAlipayByWap,
	payWXpayByWap
} from '@/api/payApi.js'
var vm = {
	name: 'pay',
	props: {
		show: {
			type: Boolean,
			default: false
		},
		isWx: {
			type: Boolean,
			default: true
		},
		price: {
			type: Number,
			default: 0
		},
		orderId: {
			type: String,
			default: ''
		}
	},
	data() {
		vm = this
		return {
			icon: {
				Uncheck,
				Checked
			},
			way: 0,
			resPayFrom: '',
			wxHref: '',
			checkIndex: 0,
			clock: true
		}
	},
	created() {

	},
	mounted() {

	},
	methods: {
		wXpay() {

		},
		checkPayNav(index) {
			this.checkIndex = index
		},
		close() {
			vm.$emit('close', false)
		},
		h5WXpay(){
			if (this.orderId !== '') {
				let data = {
					payChannelEnum: 'WEIXIN_PAY',
					payWay:'WAP_PAY',
					orderId: this.orderId
				}
				payAlipayByWap(data).then(res => {
					if (res.code === '1000') {
						location.href = res.data.wxPayResp.payUrl;
						this.resPayFrom = ''
						this.clock = true
					} else {
						T.tips(res.message || '调用支付失败')
						this.clock = true
					}
				}).catch(err => {
					T.tips(err.message || '调用支付失败')
					this.clock = true
				})
			} else {
				T.tips('订单ID不存在')
				this.clock = true
			}
		},
		h5Alipay(){
			if (this.orderId !== '') {
				let data = {
					payChannelEnum: 'ALI_PAY',
					payWay:'WAP_PAY',
					orderId: this.orderId
				}
				payAlipayByWap(data).then(res => {
					if (res.code === '1000') {
						this.wxHref = '';
						this.resPayFrom = res.data.returnHtml
						this.clock = true
						setTimeout(() => {
							document.forms["punchout_form"].submit()
						}, 300)
					} else {
						T.tips(res.message || '调用支付失败')
						this.resPayFrom = ''
						this.clock = true
					}
				}).catch(err => {
					T.tips(err.message || '调用支付失败')
					this.resPayFrom = ''
					this.clock = true
				})
			} else {
				T.tips('订单ID不存在')
				this.clock = true
			}
		},
		// 微信App支付
		AppWXpay(){
			let _this = this
			if (this.orderId !== '') {
				let data = {
					payChannelEnum: 'WEIXIN_PAY',
					payWay:'APP_PAY',
					orderId: this.orderId
				}
				payAlipayByWap(data).then(res => {
					if (res.code === '1000') {
						console.log(JSON.stringify(res))
						//let wxPayResp = res.data.wxPayResp;
						
						let wxPayResp = {
						        "appId": "wx26baf2267dd24e83",
						        "partnerId": "1558367061",
						        "prepayId": "wx21114929147847bc897abbae1647213800",
						        "packageName": "Sign=WXPay",
						        "noncestr": "XNxCt1OVWY9Y4Irf4kRETeNpXlKMjEbq",
						        "timestamp": "1571629769",
						        "sign": "238267BFCADE944D1FC5B2D0338238956F72D76257EFB5F1FC99A314829C7224",
						        "signType": null,
						        "payUrl": null,
						        "codeUrl": null,
						        "payOrderId": null,
						        "outTradeNo": null
						    }
						let obj = {
							appid: wxPayResp.appId,
							noncestr: wxPayResp.noncestr,
							package: 'Sign=WXPay', // 固定值，以微信支付文档为主
							partnerid: wxPayResp.partnerId,
							prepayid: wxPayResp.prepayId,
							timestamp: wxPayResp.timestamp,
							sign: wxPayResp.sign // 根据签名算法生成签名
						}
						let orderInfo = JSON.stringify(obj);
						console.log('orderInfo',orderInfo)
						uni.requestPayment({
						    provider: 'wxpay',
						    orderInfo, //微信、支付宝订单数据
						    success: function (res) {
								_this.clock = true
						        console.log('success:' + JSON.stringify(res));
								uni.showModal({
									content:JSON.stringify(res),
									success() {
										
									}
								})
						    },
						    fail: function (err) {
								_this.clock = true
						        console.log('fail:' + JSON.stringify(err));
								uni.showModal({
									content:JSON.stringify(err),
									success() {
										
									}
								})
						    }
						});
					} else {
						T.tips(res.message || '调用支付失败')
						this.clock = true
					}
				}).catch(err => {
					T.tips(err.message || '调用支付失败')
					this.clock = true
				})
			} else {
				T.tips('订单ID不存在')
				this.clock = true
			}
		},
		// 支付宝App支付
		AppAlipay(){
			let _this = this
			if (this.orderId !== '') {
				let data = {
					payChannelEnum: 'ALI_PAY',
					payWay:'APP_PAY',
					orderId: this.orderId
				}
				payAlipayByWap(data).then(res => {
					if (res.code === '1000') {
						console.log(JSON.stringify(res));
						let wxPayResp = res.data.wxPayResp;
						let obj = {
							appid: wxPayResp.appId,
							noncestr: wxPayResp.noncestr,
							package: 'Sign=WXPay', // 固定值，以微信支付文档为主
							partnerid: wxPayResp.partnerId,
							prepayid: wxPayResp.prepayId,
							timestamp: wxPayResp.timestamp,
							sign: wxPayResp.sign // 根据签名算法生成签名
						}
						let orderInfo = JSON.stringify(obj);
						
						
						uni.requestPayment({
						    provider: 'alipay',
						    orderInfo, //微信、支付宝订单数据
						    success: function (res) {
								_this.clock = true
						        console.log('success:' + JSON.stringify(res));
						    },
						    fail: function (err) {
								_this.clock = true
						        console.log('fail:' + JSON.stringify(err));
						    }
						});
					} else {
						T.tips(res.message || '调用支付失败')
						this.clock = true
					}
				}).catch(err => {
					T.tips(err.message || '调用支付失败')
					this.clock = true
				})
			} else {
				T.tips('订单ID不存在')
				this.clock = true
			}
		},
		doPay() {
			if(!this.clock) return
			this.clock = false
			if (this.checkIndex === 0) { // 微信网页支付




				this.AppWXpay()

			} else { // 支付宝网页支付
				




				this.AppAlipay()

			}
			this.$emit('doPay', this.way)
		}
	}
}
export default vm
